import {Component, Input, OnInit} from '@angular/core';
import {Avatar, IconService} from './service/icon.service';
import {BehaviorSubject} from 'rxjs';

@Component({
  selector: 'app-icon',
  templateUrl: './icon.component.html',
  styleUrls: ['./icon.component.sass']
})
export class IconComponent implements OnInit {
  @Input() public avatar: Avatar;
  @Input() public isActive: boolean = false;
  @Input() public viewState: string;

  private activeAvatar$: BehaviorSubject<number>;


  constructor(
    private iconService: IconService,
  ) {
    this.activeAvatar$ = this.iconService.getActiveAvatar$();
  }

  ngOnInit() {
  }

  onSelect() {
    if (this.viewState === 'avatarPreview') {
      this.activeAvatar$.next(this.avatar.index);
    }
  }

}
