import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs';


export interface Avatar {
  index: number;
  imageSrc: string;
}

export class Avatar {
  index: number;
  imageSrc: string;

  constructor(index: number, imageSrc: string) {
    this.index = index;
    this.imageSrc = imageSrc;
  }
}


@Injectable({
  providedIn: 'root'
})
export class IconService {
  private avatarListSize: number = 8;
  private activeAvatar$: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  private readonly avatarList: Avatar[];

  constructor() {
    this.avatarList = this.buildAvatarList();
  }

  buildAvatarList() {
    const avatarList = [];
    for (let i = 0; i < this.avatarListSize; i++) {
      const imageSrc: string = `/assets/img/${i}.jpg`;
      avatarList.push(new Avatar(i, imageSrc));
    }
    return avatarList;
  }

  fetchAvatarList() {
    return this.avatarList;
  }

  getActiveAvatar$ () {
    return this.activeAvatar$;
  }
}
