import {Component, Input, OnInit} from '@angular/core';
import {TimerInterface} from '../../timer-interface';
import {Avatar, IconService} from './icon/service/icon.service';
import {animate, state, style, transition, trigger} from '@angular/animations';

@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.sass'],
  animations: [
    trigger('toggleModal', [
      state('show', style({
        opacity: 1,
        zIndex: 999
      })),
      state('hide', style({
        opacity: 0,
        zIndex: -999
      })),
      transition('show => hide', animate('400ms ease-out')),
      transition('hide => show', animate('400ms ease-in')),
    ]),
  ]
})
export class TimerComponent implements OnInit {
  @Input() public global: TimerInterface;
  @Input() public local: TimerInterface;

  public avatarList: Avatar[];
  public avatar: Avatar;
  public isModalShown: boolean = false;

  public timerStates: any;

  get modal() {
    return this.isModalShown ? 'show' : 'hide';
  }


  constructor(
    private iconService: IconService,
  ) {
    this.avatarList = this.iconService.fetchAvatarList();
    this.avatar = this.avatarList[0];


  }

  ngOnInit() {
    this.timerStates = {
      global: this.global,
      local: this.local
    };
  }

  setNewAvatar(avatarIndex: number) {
    this.avatar = this.avatarList[avatarIndex];
  }

  onClose(bool: boolean) {
    this.isModalShown = bool;
  }

}
