import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {Avatar, IconService} from '../icon/service/icon.service';
import {BehaviorSubject} from 'rxjs';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.sass']
})
export class ModalComponent implements OnInit {

  public activeAvatarIndex: number;

  private activeAvatar$: BehaviorSubject<number>;

  @Input() public avatarList: Avatar[];

  @Output() private save: EventEmitter<number> = new EventEmitter();
  @Output() private close: EventEmitter<boolean> = new EventEmitter();

  constructor(
    private iconService: IconService,
  ) {
    this.activeAvatar$ = this.iconService.getActiveAvatar$();
  }

  ngOnInit() {
    this.activeAvatar$.subscribe((avatarIndex: number) => this.activeAvatarIndex = avatarIndex);
  }

  onSave() {
    this.save.emit(this.activeAvatarIndex);
    this.close.emit(false);

  }

  onClose() {
    this.close.emit(false);
  }


}
